import { createSelector } from 'reselect'
import createArtboardUrl from 'utils/createArtboardUrl'
import revisionSelectors from 'redux/modules/revisions/selectors'
import artboardSelectors from 'redux/modules/entities/artboards/selectors'
import * as R from 'ramda'

export const getUrlsForArtboardIdByType = createSelector(
  state => state.connection.token,
  (state, props) => props.artboardId,
  (state, props) =>
    revisionSelectors.getArtboardRevision(props.artboardId, state),
  (state, props) =>
    artboardSelectors.getArtboardFileTypes(props.artboardId, state),
  (token, artboardId, revision, fileTypes) => {
    return fileTypes.reduce((urlsByType, fileType) => {
      urlsByType[fileType] = R.map(
        scale =>
          createArtboardUrl(artboardId, token, scale, revision, fileType),
        R.range(1, 4)
      )
      return urlsByType
    }, {})
  }
)



