import { handleActions } from 'redux-actions'
import * as ActionTypes from 'redux/actionTypes'
import * as R from 'ramda'
import { LOCATION_CHANGE } from 'react-router-redux'

export const getInitialState = () => ({
  scrollbarDimensions: null,
  canGoBack: false
})

export default handleActions(
  {
    [ActionTypes.SET_SCROLLBAR_DIMENSIONS]: (state, { payload }) => {
      return R.set(R.lensProp('scrollbarDimensions'), payload, state)
    },
    [LOCATION_CHANGE]: (state, { payload }) => {
      if (payload && payload.state && payload.state.canGoBack === false) {
        return R.set(R.lensProp('canGoBack'), false, state)
      }

      return R.set(R.lensProp('canGoBack'), true, state)
    }
  },
  getInitialState()
)



