import { createSelector } from 'reselect'
import layerSelectors from 'redux/modules/entities/layers/selectors'
import hitAreaSelectors from 'redux/modules/hitAreas/selectors'
import screenSelectors from 'redux/modules/screen/selectors'
import { floatsToCSSRGBAString } from '@awkward/sketch-utils'

const px = x => x + 'px'

export const getHitAreaDetails = createSelector(
  (state, props) => layerSelectors.getLayer(props.layerId, state),
  (state, props) => props.scale,
  state => hitAreaSelectors.getColor(state),
  state => screenSelectors.getCanGoBack(state),
  (layer, scale, color, canGoBack) => {
    return {
      flow: layer.flow,
      width: px(Math.ceil(layer.boundingRect.width * scale)),
      height: px(Math.ceil(layer.boundingRect.height * scale)),
      top: px(layer.boundingRect.y * scale),
      left: px(layer.boundingRect.x * scale),
      color: floatsToCSSRGBAString(color),
      canGoBack
    }
  }
)



