import { combineReducers } from 'redux'
import { routerReducer } from 'react-router-redux'
import connection from 'redux/modules/connection/reducer'
import entities from 'redux/modules/entities'
import revisions from 'redux/modules/revisions/reducer'
import screen from 'redux/modules/screen/reducer'
import hitAreas from 'redux/modules/hitAreas/reducer'
import manifest from 'redux/modules/manifest/reducer'

export default combineReducers({
  connection,
  entities,
  router: routerReducer,
  revisions,
  screen,
  hitAreas,
  manifest
})



