import PropTypes from 'prop-types'
import styled from 'styled-components'
import { transparentize } from 'polished'

const borderColor = '#00fffc'
const borderRadius = '1px'

const HitArea = styled.div.attrs({
  style: props => ({
    width: props.width,
    height: props.height,
    top: props.top || 'initial',
    right: props.right || 'initial',
    bottom: props.bottom || 'initial',
    left: props.left || 'initial',
    backgroundColor: transparentize(0.8, props.color || borderColor)
  })
})`
  position: absolute;
  border-radius: ${borderRadius};
  display: block;
  opacity: ${props => (props.isActive ? '1' : '0')};
  transition: opacity 0.25s;
  cursor: pointer;
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.15);
  pointer-events: auto;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border: 2px solid ${props => props.color || borderColor};
    border-radius: ${borderRadius};
  }
`

HitArea.propTypes = {
  color: PropTypes.string
}

HitArea.defaultProps = {
  color: borderColor
}

export default HitArea



