import { createSelector } from 'reselect'
import * as R from 'ramda'
import artboardSelectors, {
  getLayers as getLayersForArtboard
} from 'redux/modules/entities/artboards/selectors'
import {
  getLayer,
  getDestinationArtboardId
} from 'redux/modules/entities/layers/selectors'

export const getRelatedArtboardIds = createSelector(
  (state, props) => props.match.params.artboardId,
  state => state.entities.artboards,
  state => state.entities.layers,
  (currentArtboardId, artboards, layers) => {
    const layerIds = getLayersForArtboard(currentArtboardId, artboards)
    const relatedArtboardIds = layerIds
      .map(layerId => {
        return getDestinationArtboardId(layerId, layers)
      })
      .filter(idOrNull => idOrNull)
    return R.uniq(relatedArtboardIds)
  }
)

export const getLayers = createSelector(
  (state, props) =>
    artboardSelectors.getArtboard(props.match.params.artboardId, state),
  state => state.entities.layers,
  (artboard, layers) => {
    return artboard
      ? artboard.layers.map(layerId => getLayer(layerId, layers))
      : []
  }
)



