import * as ActionTypes from 'redux/actionTypes'
import { document as documentSchema } from 'redux/normalizers/manifest'

const updateManifest = m => ({
  type: ActionTypes.UPDATE_MANIFEST,
  payload: {
    manifest: m
  },
  meta: {
    schema: documentSchema
  }
})

export const actions = {
  updateManifest
}



