import * as R from 'ramda'
import { schema } from 'normalizr'

/**
 * A Layer requires this generated id because a manifest can contain multiple Layers with the same uuid.
 * This happens with symbols which share the same Layer having a flow.
 * In this case we use a combined id which is derived from the parent id (Symbol or Artboard id),
 * the Layer id itself and the index of the Layer in the array of Layers a Symbol or Artboard can have.
 * The index is necessary because an Artboard can have more than 1 instance of the same symbol, if
 * that is the case the Artboard id and Layer id will be the same.
 * This generated id is purely based on info from the manifest and should always be the same if re-generated.
 */
const layer = new schema.Entity(
  'layers',
  {},
  {
    idAttribute: (value, parent, key) =>
      `${parent.id}-${value.id}-${parent[key].indexOf(value)}`,
    processStrategy: (value, parent, key) =>
      R.set(
        R.lensProp('id'),
        `${parent.id}-${value.id}-${parent[key].indexOf(value)}`,
        value
      )
  }
)

const artboard = new schema.Entity('artboards', {
  layers: [layer]
})

const page = new schema.Entity('pages', {
  artboards: [artboard]
})

export const document = new schema.Entity(
  'documents',
  {
    pages: [page]
  },
  {
    idAttribute: value => {
      return value.manifest.id
    },
    processStrategy: (value, parent, key) => {
      const pages = R.pathOr([], ['manifest', 'contents', 'pages'], value)
      const flowColor = R.pathOr(null, ['manifest', 'flowColor'], value)

      return {
        flowColor,
        pages
      }
    }
  }
)



