import * as R from 'ramda'
import globalizeSelectors from 'utils/globalizeSelectors'

const localState = R.path(['connection'])

export const getConnectionIsReady = state =>
  state.connected && state.manifestLoaded

export const getToken = state => R.propOr(null, 'token', state)

export default globalizeSelectors(localState, {
  getConnectionIsReady,
  getToken
})



