import { handleActions } from 'redux-actions'
import * as ActionTypes from 'redux/actionTypes'

export const getInitialState = () => ({
  connected: false,
  token: null,
  manifestLoaded: false
})

export default handleActions(
  {
    [ActionTypes.CONNECTION_SUCCESS]: (state, { payload }) => {
      return { ...state, connected: true, token: payload.token }
    },
    [ActionTypes.CONNECTION_CLOSED]: state => {
      return { ...getInitialState() }
    },
    [ActionTypes.UPDATE_MANIFEST]: state => {
      return { ...state, manifestLoaded: true }
    }
  },
  getInitialState()
)



