import * as R from 'ramda'
import browser from 'detect-browser'

const BROWSER_NAME_MAPPING = {
  edge: 'Edge',
  chrome: 'Chrome',
  crios: 'Chrome iOS',
  firefox: 'Firefox',
  opera: 'Opera',
  ie: 'Internet Explorer',
  android: 'Android',
  ios: 'iOS',
  safari: 'Safari'
}

export const getBrowserName = () => (browser ? browser.name : 'Unknown')

export const getPrettyBrowserNamePure = name =>
  R.propOr(name, name, BROWSER_NAME_MAPPING)

export const getPrettyBrowserName = () =>
  getPrettyBrowserNamePure(getBrowserName())

export default getPrettyBrowserName



