import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { compose, withState, withHandlers, withProps, toClass } from 'recompose'
import styled from 'styled-components'
import { connect } from 'react-redux'
import HitArea from 'containers/HitArea'
import artboardSelectors from 'redux/modules/entities/artboards/selectors'
import screenSelectors from 'redux/modules/screen/selectors'
import { getLayers, getRelatedArtboardIds } from './selectors'
import { getUrlsForArtboardIdByType } from 'containers/PrefetchArtboardImages/selectors'
import { withRouter } from 'react-router-dom'
import * as R from 'ramda'
import {
  ArtboardsTransition,
  Artboard,
  Viewport
} from '@awkward/sketch-prototyping'
import { floatsToCSSRGBAString } from '@awkward/sketch-utils'
import { withContentRect } from 'react-measure'
import FallbackState from 'components/FallbackState'
import isSketchWebView from 'utils/isSketchWebView'
import { actions as artboardActions } from 'redux/modules/entities/artboards'
import createSrcSet from 'utils/createSrcSet'
import AddGlobalTransitionToArtboardHandler from 'containers/AddGlobalTransitionToArtboardHandler'
import PrefetchArtboardImages from 'containers/PrefetchArtboardImages'

const flowViewBackgroundColor = '#000'

const Container = toClass(styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-width: 100vw;
  max-height: 100vh;
  background-color: ${flowViewBackgroundColor};

  display: flex;
  justify-content: center;
  align-items: center;
`)

const smallest = (...values) => R.head(R.sort((a, b) => a - b, values))

class FlowView extends Component {
  static propTypes = {
    artboard: PropTypes.object,
    flashHitareas: PropTypes.func.isRequired,
    hitAreasAreActive: PropTypes.bool.isRequired,
    viewportWidth: PropTypes.number.isRequired,
    viewportHeight: PropTypes.number.isRequired,
    scrollbarDimensions: PropTypes.object.isRequired,
    setCurrentArtboard: PropTypes.func,
    scrollableArtboardUrls: PropTypes.array,
    fixedViewportUrls: PropTypes.array,
    relatedArtboardUrls: PropTypes.array,
    isSketchWebView: PropTypes.bool.isRequired,
    shouldHideScrollbars: PropTypes.bool.isRequired,
    artboardUrls: PropTypes.object.isRequired,
    relatedArtboardIds: PropTypes.arrayOf(PropTypes.string).isRequired
  }

  componentDidMount() {
    const { viewportWidth, viewportHeight, setCurrentArtboard } = this.props
    const artboardId = R.pathOr(
      null,
      ['match', 'params', 'artboardId'],
      this.props
    )
    if ((!viewportWidth || !viewportHeight) && artboardId) {
      setCurrentArtboard(artboardId)
    }
  }

  componentDidUpdate(prevProps) {
    const { artboard } = prevProps

    if (
      artboard &&
      this.props.artboard &&
      this.props.artboard.id !== artboard.id
    ) {
      if (isSketchWebView()) {
        window.webkit.messageHandlers.artboardDidChange.postMessage(
          this.props.artboard.id
        )
      }
    }
  }

  calculateScale(
    [containerWidth, containerHeight],
    [viewportWidth, viewportHeight]
  ) {
    const x =
      containerWidth < viewportWidth ? containerWidth / viewportWidth : 1
    const y =
      containerHeight < viewportHeight ? containerHeight / viewportHeight : 1

    return smallest(x, y)
  }

  calculateViewport(scale) {
    const {
      viewportWidth,
      viewportHeight,
      isSketchWebView,
      scrollbarDimensions
    } = this.props

    const addScrollbarDimensions = R.ifElse(
      () => !isSketchWebView,
      R.zipWith(R.add, [scrollbarDimensions.width, scrollbarDimensions.height]),
      R.identity
    )

    const calculateViewport = R.pipe(
      R.map(R.pipe(R.multiply(scale), Math.floor)),
      addScrollbarDimensions
    )

    return calculateViewport([viewportWidth, viewportHeight])
  }

  renderArtboardNotFound() {
    return (
      <FallbackState
        width="100vw"
        height="100vh"
        title="Artboard not found"
        message="Select an Artboard to preview."
        iconType="artboard"
      />
    )
  }

  render() {
    const {
      viewportWidth,
      viewportHeight,
      artboard,
      flashHitareas,
      hitAreasAreActive,
      layers,
      match,
      measureRef,
      contentRect,
      shouldHideScrollbars,
      scrollbarDimensions,
      scrollableArtboardUrls,
      fixedViewportUrls,
      artboardUrls,
      relatedArtboardIds,
      ...props
    } = this.props

    if (!artboard) return this.renderArtboardNotFound()

    const containerWidth = contentRect.entry.width || window.innerWidth
    const containerHeight = contentRect.entry.height || window.innerHeight
    const scale = this.calculateScale(
      [containerWidth, containerHeight],
      [viewportWidth, viewportHeight]
    )
    const [finalViewportWidth, finalViewportHeight] = this.calculateViewport(
      scale
    )

    return (
      <Container innerRef={measureRef} onClick={flashHitareas} {...props}>
        {relatedArtboardIds.map(artboardId => (
          <PrefetchArtboardImages key={artboardId} artboardId={artboardId} />
        ))}
        <AddGlobalTransitionToArtboardHandler />
        <Viewport width={finalViewportWidth} height={finalViewportHeight}>
          <ArtboardsTransition>
            <Artboard
              scrollbarWidth={scrollbarDimensions.width}
              scrollbarHeight={scrollbarDimensions.height}
              hideScrollbarsOnWebkit={shouldHideScrollbars}
              viewportWidth={finalViewportWidth}
              viewportHeight={finalViewportHeight}
              contentsWidth={Math.floor(artboard.width * scale)}
              contentsHeight={Math.floor(artboard.height * scale)}
              backgroundColor={
                floatsToCSSRGBAString(artboard.backgroundColor) || '#fff'
              }
              fixedChildren={
                <React.Fragment>
                  {artboardUrls.fixed && (
                    <img
                      src={artboardUrls.fixed[0]}
                      srcSet={createSrcSet(artboardUrls.fixed)}
                      style={{ width: '100%' }}
                      draggable={false}
                      alt=""
                    />
                  )}
                  {!!layers.filter(l => l.isFixedToViewport).length &&
                    layers
                      .filter(l => l.isFixedToViewport)
                      .map(layer => (
                        <HitArea
                          key={layer.id}
                          layerId={layer.id}
                          isActive={hitAreasAreActive}
                          scale={scale}
                        />
                      ))}
                </React.Fragment>
              }>
              {layers
                .filter(l => !l.isFixedToViewport)
                .map(layer => (
                  <HitArea
                    key={layer.id}
                    layerId={layer.id}
                    isActive={hitAreasAreActive}
                    scale={scale}
                  />
                ))}
              {artboardUrls.scrollable || artboardUrls.full ? (
                <img
                  src={
                    artboardUrls.scrollable
                      ? artboardUrls.scrollable[0]
                      : artboardUrls.full[0]
                  }
                  srcSet={createSrcSet(
                    artboardUrls.scrollable || artboardUrls.full
                  )}
                  style={{ width: '100%' }}
                  draggable={false}
                  alt=""
                />
              ) : null}
            </Artboard>
          </ArtboardsTransition>
        </Viewport>
      </Container>
    )
  }
}

const mapStateToProps = (state, props) => {
  const artboardId = props.match.params.artboardId
  const artboard = artboardSelectors.getArtboard(artboardId, state)
  const viewport = artboardSelectors.getViewport(artboardId, state)

  return {
    artboard,
    layers: getLayers(state, props),
    viewportWidth: R.prop('width', viewport),
    viewportHeight: R.prop('height', viewport),
    shouldHideScrollbars: screenSelectors.getShouldHideScrollbars(state),
    scrollbarDimensions: screenSelectors.getScrollbarDimensions(state),
    artboardUrls: getUrlsForArtboardIdByType(state, props),
    relatedArtboardIds: getRelatedArtboardIds(state, props)
  }
}

const mapDispatchToProps = dispatch => ({
  setCurrentArtboard: artboardId =>
    dispatch(artboardActions.setCurrentArtboard(artboardId))
})

const enhance = compose(
  withState('hitAreasAreActive', 'setHitAreasAreActive', false),
  withHandlers({
    flashHitareas: ({ setHitAreasAreActive, hitAreasAreActive }) => () => {
      if (hitAreasAreActive) return
      setHitAreasAreActive(true)
      setTimeout(() => {
        setHitAreasAreActive(false)
      }, 500)
    }
  }),
  withRouter,
  withProps(props => ({
    isSketchWebView: isSketchWebView(),
    artboardId: R.pathOr(null, ['match', 'params', 'artboardId'], props)
  })),
  connect(mapStateToProps, mapDispatchToProps),
  withContentRect()
)

export default enhance(FlowView)



