import React from 'react'
import ReactDOM from 'react-dom'
import { Provider } from 'react-redux'
import createHistory from 'history/createBrowserHistory'
import { ConnectedRouter } from 'react-router-redux'
import registerServiceWorker from './registerServiceWorker'
import configureStore from 'redux/configureStore'
import App from 'containers/App'
import './globalStyles'
import isUUID from 'is-uuid'
import { actions as artboardActions } from 'redux/modules/entities/artboards'

if (process.env.REACT_APP_COMMIT_HASH) {
  window.__RELEASE_INFO = {
    commit: process.env.REACT_APP_COMMIT_HASH
  }
}

const history = createHistory()
const store = configureStore({ history })

window.goToArtboard = artboardId =>
  isUUID.anyNonNil(artboardId) &&
  store.dispatch(artboardActions.setCurrentArtboard(artboardId))
window.getSessionId = () => window.localStorage.getItem('uuid')

ReactDOM.render(
  <Provider store={store}>
    <ConnectedRouter history={history}>
      <App />
    </ConnectedRouter>
  </Provider>,
  document.getElementById('root')
)
registerServiceWorker()



