import { handleActions } from 'redux-actions'
import * as ActionTypes from 'redux/actionTypes'
import * as R from 'ramda'
import incOrDefaultToZero from 'utils/incOrDefaultToZero'

export const incRevisionForArtboardId = R.curry((artboardId, state) =>
  R.over(R.lensPath(['artboards', artboardId]), incOrDefaultToZero, state)
)

export const getInitialState = () => ({ artboards: {} })

export default handleActions(
  {
    [ActionTypes.UPDATE_MANIFEST]: (state, action) => {
      const artboardIds = R.keys(action.payload.entities.artboards)
      return R.reduce(R.flip(incRevisionForArtboardId), state, artboardIds)
    },
    [ActionTypes.INCREASE_ARTBOARD_REVISION]: (state, action) => {
      const artboardId = action.payload
      return incRevisionForArtboardId(artboardId, state)
    }
  },
  getInitialState()
)



