const loadImage = ({ src, srcSet }) => {
  return new Promise((resolve, reject) => {
    let image = new window.Image()
    image.addEventListener('load', e => {
      resolve(e)
    })
    image.addEventListener('error', e => {
      reject(e)
      image = null
    })
    image.src = src

    if (srcSet) image.srcset = srcSet
  })
}

export default loadImage



