import React, { Component } from 'react'
import { connect } from 'react-redux'
import ConnectionManager from 'containers/ConnectionManager'
import FlowView from 'containers/FlowView'
import connectionSelectors from 'redux/modules/connection/selectors'
import config from 'config'
import { withRouter, Route, Switch } from 'react-router-dom'
import { SocketConnection } from '@awkward/react-websocket'
import FallbackState from 'components/FallbackState'
import ScrollbarSize from 'react-scrollbar-size'
import screenActions from 'redux/modules/screen/actions'
import screenSelectors from 'redux/modules/screen/selectors'
import manifestSelectors from 'redux/modules/manifest/selectors'
import artboardsSelectors from 'redux/modules/entities/artboards/selectors'
import { replace } from 'react-router-redux'

const mapStateToProps = (state, props) => ({
  connectionIsReady: connectionSelectors.getConnectionIsReady(state),
  isScrollbarDimensionsSet: screenSelectors.getIsScrollbarDimensionsSet(state),
  manifestIsSet: manifestSelectors.getManifestIsSet(state),
  documentContainsArtboards: artboardsSelectors.getArtboards(state).length > 0
})

const mapDispatchToProps = dispatch => ({
  setScrollbarDimensions: measurements =>
    dispatch(
      screenActions.setScrollbarDimensions({
        width: measurements.scrollbarWidth,
        height: measurements.scrollbarHeight
      })
    ),
  goToConnectionError: () => dispatch(replace('/state/connection-error'))
})

class App extends Component {
  render() {
    const {
      connectionIsReady,
      setScrollbarDimensions,
      isScrollbarDimensionsSet,
      goToConnectionError,
      manifestIsSet,
      documentContainsArtboards
    } = this.props

    return (
      <SocketConnection uri={config.socketUri}>
        <div>
          <ConnectionManager handleError={goToConnectionError} />
          <ScrollbarSize onLoad={setScrollbarDimensions} />
          {connectionIsReady ? (
            isScrollbarDimensionsSet &&
            manifestIsSet && (
              <Switch>
                {documentContainsArtboards && (
                  <Route path="/:artboardId" component={FlowView} />
                )}
                <Route
                  render={() => (
                    <FallbackState
                      width="100vw"
                      height="100vh"
                      title="No Artboards"
                      message="Artboards are required in order to preview your designs."
                      iconType="artboard"
                    />
                  )}
                />
              </Switch>
            )
          ) : (
            <Switch>
              <Route
                path="/state/connection-error"
                render={() => (
                  <FallbackState
                    width="100vw"
                    height="100vh"
                    title="Connection Error"
                    message="Error establishing connection"
                    iconType="warn"
                  />
                )}
              />
            </Switch>
          )}
        </div>
      </SocketConnection>
    )
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App))



