import globalizeSelectors from 'utils/globalizeSelectors'
import * as R from 'ramda'

const localState = R.path(['screen'])

export const getScalingMode = state => state.scalingMode
export const getIsScrollbarDimensionsSet = state =>
  state.scrollbarDimensions !== null
export const getScrollbarDimensions = state => state.scrollbarDimensions
export const getShouldHideScrollbars = state =>
  !!state.scrollbarDimensions.width || !!state.scrollbarDimensions.height
export const getCanGoBack = state => state.canGoBack

export default globalizeSelectors(localState, {
  getScalingMode,
  getIsScrollbarDimensionsSet,
  getScrollbarDimensions,
  getShouldHideScrollbars,
  getCanGoBack
})



