import { handleActions } from 'redux-actions'
import * as ActionTypes from 'redux/actionTypes'
import { replace } from 'react-router-redux'

const getInitialState = () => ({
  byId: {}
})

const setCurrentArtboard = artboardId => (dispatch, getState) => {
  dispatch(
    replace({
      pathname: `/${artboardId}`,
      state: { transitionType: 'none', canGoBack: false }
    })
  )
}

export const actions = {
  setCurrentArtboard
}

export default handleActions(
  {
    [ActionTypes.UPDATE_MANIFEST]: (state, action) => {
      return { byId: action.payload.entities.artboards }
    }
  },
  getInitialState()
)



