import React, { Component } from 'react'
import { withSocket, Subscribe } from '@awkward/react-websocket'
import { connect } from 'react-redux'
import * as R from 'ramda'
import { v4 as generateUUID } from 'uuid'
import queryString from 'query-string'
import { actions as connectionActions } from 'redux/modules/connection'
import { actions as revisionActions } from 'redux/modules/revisions/actions'
import { actions as manifestActions } from 'redux/modules/manifest/actions'
import getBrowserName from 'utils/getBrowserName'

const mapStateToProps = state => ({
  connected: state.connection.connected
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  connectionSuccess: token => dispatch(connectionActions.success(token)),
  updateManifest: manifest =>
    dispatch(manifestActions.updateManifest(manifest)),
  updateArtboard: artboardId =>
    dispatch(revisionActions.increaseArtboardRevision(artboardId)),
  closeConnection: () => {
    dispatch(connectionActions.close())
  }
})

class ConnectionManager extends Component {
  componentDidMount() {
    this.initConnection()
  }

  initConnection = () => {
    let uuid = localStorage.getItem('uuid')

    if (!uuid) {
      uuid = generateUUID()
      localStorage.setItem('uuid', uuid)
    }

    this.setState({
      uuid
    })
  }

  sendDeviceInfo = () => {
    const { socketDispatcher } = this.props
    const { uuid } = this.state

    const msg = {
      type: 'device-info',
      content: {
        uuid,
        'user-agent': window.navigator.userAgent,
        'display-name': getBrowserName(),
        challenge: queryString.parse(window.location.search).challenge,
        screens: [
          {
            name: 'Browser',
            scale: window.devicePixelRatio,
            width: window.screen.width,
            height: window.screen.height
          }
        ]
      }
    }

    socketDispatcher.send(JSON.stringify(msg))
  }

  connect = () => {
    if (!this.props.connected) this.props.socketDispatcher.connect()
  }

  handleOpen = () => {
    this.sendDeviceInfo()
  }

  handleClose = () => {
    this.props.closeConnection()
    setTimeout(this.connect, 5000)
  }

  handleError = () => {
    this.props.handleError()
  }

  handleManifestUpdate = e => {
    this.props.updateManifest(e.content)
  }

  handleArtboardUpdate = e => {
    if (R.isNil(e.content.identifier)) return

    this.props.updateArtboard(e.content.identifier)
  }

  handleConnectionSuccess = e => {
    this.props.connectionSuccess(e.content.token)
  }

  render() {
    return (
      <div>
        <Subscribe onOpen={this.handleOpen} />
        <Subscribe onClose={this.handleClose} />
        <Subscribe onError={this.handleError} />
        <Subscribe event="connected" handler={this.handleConnectionSuccess} />
        <Subscribe event="manifest" handler={this.handleManifestUpdate} />
        <Subscribe event="artboard" handler={this.handleArtboardUpdate} />
      </div>
    )
  }
}

ConnectionManager.propTypes = {}

export default R.compose(
  withSocket,
  connect(mapStateToProps, mapDispatchToProps)
)(ConnectionManager)



