import config from 'config'

const createArtboardUrl = (
  selectedArtboardId,
  token,
  scale,
  revision = 0,
  type = 'full'
) =>
  `${
    config.fileServerUri
  }/artboards/${selectedArtboardId}?scale=${scale}&token=${token}&v=${revision}&type=${type}`

export default createArtboardUrl



