import { handleActions } from 'redux-actions'
import * as ActionTypes from 'redux/actionTypes'
import * as R from 'ramda'

export const getInitialState = () => ({ color: null })

export default handleActions(
  {
    [ActionTypes.UPDATE_MANIFEST]: (state, action) => {
      const flowColor = R.pathOr(
        null,
        [
          'payload',
          'entities',
          'documents',
          action.payload.result,
          'flowColor'
        ],
        action
      )

      return {
        color: flowColor
      }
    }
  },
  getInitialState()
)



