import * as ActionTypes from 'redux/actionTypes'

const increaseArtboardRevision = artboardId => {
  return {
    payload: artboardId,
    type: ActionTypes.INCREASE_ARTBOARD_REVISION
  }
}

export const actions = {
  increaseArtboardRevision
}



