import globalizeSelectors from 'utils/globalizeSelectors'
import * as R from 'ramda'

const localState = R.path(['entities', 'layers'])

export const getLayer = (id, state) => state.byId[id]

export const getDestinationArtboardId = (id, state) => {
  return R.pathOr(null, ['flow', 'destinationArtboardID'], getLayer(id, state))
}

export default globalizeSelectors(localState, {
  getLayer,
  getDestinationArtboardId
})



