import { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import * as R from 'ramda'
import { goToArtboard } from 'containers/HitArea'

const noop = () => {}

const mapDispatchToProps = (dispatch, props) => {
  return {
    goToArtboard: goToArtboard(dispatch, props)
  }
}

class AddGlobalTransitionToArtboardHandler extends Component {
  componentDidMount() {
    window.transitionToArtboard = (destinationArtboardId, animationType) =>
      this.props.goToArtboard(destinationArtboardId, animationType)
  }

  componentWillUnmount() {
    window.transitionToArtboard = noop
  }

  render() {
    return null
  }
}

AddGlobalTransitionToArtboardHandler.propTypes = {
  goToArtboard: PropTypes.func.isRequired
}

export default R.compose(withRouter, connect(null, mapDispatchToProps))(
  AddGlobalTransitionToArtboardHandler
)



