import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { getUrlsForArtboardIdByType } from './selectors'
import FetchImage from 'components/FetchImage'
import createSrcSet from 'utils/createSrcSet'

const mapStateToProps = (state, props) => {
  return { urls: getUrlsForArtboardIdByType(state, props) }
}

const PrefetchArtboardImages = ({ urls: { full, scrollable, fixed } }) => {
  return [full, scrollable, fixed]
    .filter(arr => arr && arr.length)
    .map(urls => (
      <FetchImage key={urls[0]} src={urls[0]} srcSet={createSrcSet(urls)} />
    ))
}

PrefetchArtboardImages.propTypes = {
  urls: PropTypes.shape({
    full: PropTypes.array,
    scrollable: PropTypes.array,
    fixed: PropTypes.array
  }).isRequired
}

export default connect(mapStateToProps)(PrefetchArtboardImages)



