import React from 'react'
import PropTypes from 'prop-types'
import styled from 'styled-components'
import ArtboardIcon from './artboard.svg'
import WarningIcon from './warning.svg'

const Container = styled.div`
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  line-height: 1.5;
  width: ${props => props.width};
  height: ${props => props.height};
`

const Title = styled.h1`
  font-size: 0.6875rem;
  margin: 0;
  user-select: none;
`

const Message = styled.p`
  font-size: 0.6875rem;
  opacity: 0.8;
  max-width: 170px;
  margin: 0;
  user-select: none;
`

const StyledImg = styled.img`
  margin-bottom: 0.9375rem;
  user-select: none;
`

const Icon = ({ type }) => {
  switch (type) {
    case 'warn':
      return <StyledImg src={WarningIcon} alt="Warning" />
    case 'artboard':
    default:
      return <StyledImg src={ArtboardIcon} alt="Artboard" />
  }
}

Icon.propTypes = {
  type: PropTypes.string
}

const FallbackState = ({ title, message, width, height, iconType }) => (
  <Container width={width} height={height}>
    <Icon type={iconType} />
    <Title>{title}</Title>
    <Message>{message}</Message>
  </Container>
)

FallbackState.propTypes = {
  title: PropTypes.string,
  message: PropTypes.string,
  width: PropTypes.string,
  height: PropTypes.string,
  iconType: PropTypes.oneOf(['artboard', 'error'])
}

export default FallbackState



